<template>
    <div class="container setting">
        <div class="inner">
            <!-- tab_item 클릭시 active 클래스 토글되는 형태입니다 -->
            <!-- tab_menu -->
            <div class="tab_box type-a">
                <ul>
                    <li class="tab_item active" @click="moveTab('timeSet')"><span>야간시간대 설정</span></li>
                    <li class="tab_item" @click="moveTab('eventSet')"><span>이벤트 항목 설정</span></li>
                    <!-- <li class="tab_item"><span>날씨정보 매핑</span></li> -->
                </ul>
            </div>
            <!-- //tab_menu -->               
            <!-- content_box left -->
            <div class="content_box left w50p" v-if="dataset">
                <div class="content_row setting_time">
                    <template 
                        v-for=" (mon, monIdx) in 6" 
                    >                        
                        <div class="content_item" :key="monIdx">                            
                            <h5 class="content_item_tit">{{mon}}월</h5>
                            <div class="dis_ib sunset">
                                <span class="content_item_txt">일몰시간</span>
                                <div class="input_box">
                                    <select :id="'sunsetHour'+mon"  class="select" :ref="'sunsetHour'+mon" v-model="dataset[mon].sunsetHour">                                    
                                        <template 
                                            v-for="(t , tidx) in 25"                                            
                                        >
                                        <option :value="'0'+tidx" :key =tidx v-if="tidx < 10">0{{tidx}}시</option>
                                        <option :value="tidx" :key =tidx v-else>{{tidx}}시</option>
                                        </template>
                                    </select>                                
                                </div>
                                <div class="input_box">
                                    <select  :id="'sunsetMin'+mon" class="select" :ref="'sunsetMin'+mon" v-model="dataset[mon].sunsetMin">
                                        <template 
                                            v-for="(m , mIdx) in 61"                                            
                                        >
                                            <option :value="'0'+mIdx" :key="mIdx" v-if="mIdx < 10">0{{mIdx}}분</option>
                                            <option :value="mIdx" :key="mIdx" v-else >{{mIdx}}분</option>
                                        </template>
                                    </select>
                                </div>                            
                            </div>
                            <div class="dis_ib sunrise">
                                <span class="content_item_txt">일출시간</span>
                                <div class="input_box">
                                    <select :id="'sunriseHour'+mon"  class="select" :ref="'sunriseHour'+mon" v-model="dataset[mon].sunriseHour">                                    
                                        <template 
                                            v-for="(t , tidx) in 25"                                            
                                        >
                                        <option :value="'0'+tidx" :key =tidx v-if="tidx < 10">0{{tidx}}시</option>
                                        <option :value="tidx" :key =tidx v-else>{{tidx}}시</option>
                                        </template>
                                    </select>                                
                                </div>
                                <div class="input_box">
                                    <select  :id="'sunriseMin'+mon" class="select" :ref="'sunriseMin'+mon" v-model="dataset[mon].sunriseMin">
                                        <template 
                                            v-for="(m , mIdx) in 61"                                            
                                        >
                                            <option :value="'0'+mIdx" :key="mIdx" v-if="mIdx < 10">0{{mIdx}}분</option>
                                            <option :value="mIdx" :key="mIdx" v-else >{{mIdx}}분</option>
                                        </template>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </template>
                </div>
            </div>
            <div class="content_box right w50p ml0" v-if="dataset">
                <div class="content_row setting_time">
                    <template 
                        v-for=" (mon1, mIdx1) in 12" 
                    >                        
                        <div class="content_item" :key ="mIdx1" v-if="mon1 > 6">                            
                            <h5 class="content_item_tit">{{mon1}}월</h5>
                            <div class="dis_ib sunset">
                                <span class="content_item_txt">일몰시간</span>
                                <div class="input_box">
                                    <select :id="'sunsetHour'+mon1"  class="select" :ref="'sunsetHour'+mon1" v-model="dataset[mon1].sunsetHour">
                                        <template 
                                            v-for="(t , tidx) in 25"                                            
                                        >
                                        <option :value="'0'+tidx" :key =tidx v-if="tidx < 10">0{{tidx}}시</option>
                                        <option :value="tidx" :key =tidx v-else>{{tidx}}시</option>
                                        </template>
                                    </select>                                
                                </div>
                                <div class="input_box">
                                    <select  :id="'sunsetMin'+mon1" class="select" :ref="'sunsetMin'+mon1" v-model="dataset[mon1].sunsetMin"> 
                                        <template 
                                            v-for="(min , minIdx) in 61"                                            
                                        >
                                            <option :value="'0'+minIdx" :key="minIdx" v-if="minIdx < 10">0{{minIdx}}분</option>
                                            <option :value="minIdx" :key="minIdx" v-else >{{minIdx}}분</option>
                                        </template>
                                    </select>
                                </div>                            
                            </div>
                            <div class="dis_ib sunrise">
                                <span class="content_item_txt">일출시간</span>
                                <div class="input_box">
                                    <select :id="'sunriseHour'+mon1"  class="select" :ref="'sunriseHour'+mon1" v-model="dataset[mon1].sunriseHour">
                                        <template 
                                            v-for="(t , tidx) in 25"                                            
                                        >
                                        <option :value="'0'+tidx" :key =tidx v-if="tidx < 10">0{{tidx}}시</option>
                                        <option :value="tidx" :key =tidx v-else>{{tidx}}시</option>
                                        </template>
                                    </select>                                
                                </div>
                                <div class="input_box">
                                    <select  :id="'sunriseMin'+mon1" class="select" :ref="'sunriseMin'+mon1" v-model="dataset[mon1].sunriseMin">
                                        <template 
                                            v-for="(min1 , minIdx1) in 61"                                            
                                        >
                                            <option :value="'0'+minIdx1" :key="minIdx1" v-if="minIdx1 < 10">0{{minIdx1}}분</option>
                                            <option :value="minIdx1" :key="minIdx1" v-else >{{minIdx1}}분</option>
                                        </template>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </template>
                </div>
            </div>
            <!-- // content_box right -->
            <div class="input_box txt_c w100p">
                <button class="btn btn_smd btn_primary" @click="fnSave">조건 저장</button>
            </div>
        </div>        
    </div>
    <!-- container -->
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
    data(){
        return {
            dataset: {},          
        }
    }
    ,created(){
        this.setDefault();
    },    
    computed: {
      ...mapState('setting', ['tResData'])  
    },
    mounted() {
        this.fnSearch();
    },
    methods :
    {
        setDefault()
       {
            var dataObj ={}
            for(var i=1;i<13;i++){                            
                dataObj[i] = { sunsetHour: "18" , sunsetMin: "30" , sunriseHour: "06", sunriseMin : "30" }
            }
            this.dataset = dataObj            
        }
    ,   ...mapActions('setting', ['saveSetTimes','getNightTimes'])    
    ,   async fnSave()
        {               
            const conResult = await this.$confirm("저장 하시겠습니까?") 
            if(conResult)
            {
                
                const result = await this.saveSetTimes(this.dataset);                  
                
                if(result.status == 200) 
                { 
                    this.$info("저장 되었습니다.","확인");
                    this.fnSearch();
                }  
            }
            
        }
    ,   isNull(str)
        {
            if(str == undefined || str == "" || str == null)
            {
                return true;
            }   
            else
            {
                return false;
            }
        }
    ,   async fnSearch(){
            const result = await this.getNightTimes();            
            const data = result.data;
            if(this.isNull(data) == false)
            {
                if(data.length > 0)
                {
                    for(var d in data)
                    {
                        var obj = data[d];
                        var sunSet = obj.sunsetTime;
                        var sunRise = obj.sinriseTime;
                        var selMon = parseInt(obj.mnth);                    
                        this.dataset[selMon].sunsetHour = sunSet.substring(0,2)
                        this.dataset[selMon].sunsetMin = sunSet.substring(2,4)
                        this.dataset[selMon].sunriseHour = sunRise.substring(0,2)
                        this.dataset[selMon].sunriseMin = sunRise.substring(2,4)
                    }
                }
            }
        }
    ,   moveTab(urlName){
            this.$router.push({name:urlName})
        }
    }
}
</script>




